/* Card de produit */
.productPreview {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.productCard {
  width: 22%;
  height: 350px;
  margin: 10px 0;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  box-shadow: none;
  position: relative;
}

.productCard:hover button {
  opacity: 1;
  color: #909090;
  padding: 10px 15px;
  top: 50%;
}
.productCard button:hover {
  background-color: #4d4d4d;
  color: white;
}
.productCard button {
  background-color: white;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.51, 0.92, 0.24, 1.15);
}
.productCard div:nth-child(1):hover > img {
  transform: scale(1.2);
}

.productCard div:nth-child(1) {
  display: flex;
  height: 80%;
  overflow: hidden !important;
  background-color: #ccc;
}
.productCard div:nth-child(1) img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.4s cubic-bezier(0.51, 0.92, 0.24, 1.15);
}
.productCard div:nth-child(2) {
  height: 20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #808080;
}
.productCard div:nth-child(2) p {
  font-family: "Poppins", sans-serif !important;
  width: 100%;
  overflow: hidden;
}
.productCard div:nth-child(2) p span {
  white-space: nowrap;
  overflow: hidden;
  width: 80%;
  display: block;
  text-overflow: ellipsis;
}
.productCard div:nth-child(2) div svg:nth-child(2) {
  margin-left: 5px;
}
/* Fin card de produit */
