.checkoutPart {
}
.coTotal {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.coForm {
  width: 80%;
  margin: 30px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.coForm .formSection {
  display: flex;
  flex-direction: column;
  width: 45%;
  margin-bottom: 30px;
}

.coForm .formBtn {
  width: 100%;
  text-align: center;
}
.coForm .formBtn button {
  cursor: pointer;
  color: white;
  width: 50%;
  margin: auto;
  padding: 15px 0;
}
.coBtnEnable {
  background-color: var(--pricipalColor) !important;
}

.coForm select {
  background-color: transparent;
  border: solid 1px #ccc;
  padding: 20px 0px;
  border-radius: 5px;
}
.errorField {
  color: red;
}

.coForm h3 {
  width: 100%;
  text-align: center;
  margin: 40px 0;
}
