.searchOnStorePart {
  width: 90%;
  max-height: 0;
  overflow: hidden;
  transition: all 0.4s ease;
}
.searchOnStorePart input {
  width: 100%;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
  padding: 15px 10px;
}
.searchOnStorePartVisible {
  overflow: initial;
  max-height: 100px;
  /* margin: 10px auto; */
}
.filterOrSearchBtns {
  margin: 10px 0;
}
