.cartPart {
}
.emptyCart {
  width: 100%;
  background-color: #ccc;
  padding: 30px 0;
  text-align: center;
  font-weight: bold;
}
.cpList {
  width: 80%;
  margin: 50px auto;
  /* background-color: pink; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.cpList h2 {
  width: 25%;
  border-bottom: solid 1px #ccc;
  text-align: center;
  border-right: solid 1px #ccc;
}
.cpList h2:first-child,
.cplRow section:first-child {
  border-left: solid 1px #ccc;
}
.cplRow {
  width: 100%;
  height: 150px;
  display: flex;
  border-bottom: solid 1px #ccc;
}
.cplRow section {
  height: 100%;
  text-align: center;
  width: 100%;
  border-right: solid 1px #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cplProduct {
  flex-direction: column;
  overflow: hidden;
}
.cplProduct img {
  max-width: 80%;
  max-height: 60%;
  background-color: #ccc;
  padding: 0 10px;
}
.cplProduct p {
  max-width: 90%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.cplQuantity {
  flex-direction: column;
}
.cplQuantity .cplDelete {
  /* width: 100%; */
}
/*  */

/* Partie du calcul dyu prix du pannier */
.cpTotal {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.cptHeader {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cptHeader a {
  color: white;
  padding: 10px 15px;
  background-color: var(--pricipalColor);
}
.cptHeader h1 {
  text-decoration: underline;
  color: var(--pricipalColor);
}
.cptCoupon,
.cptSum {
  width: 40%;
}
.cptCoupon {
}
.cptCoupon {
  margin-bottom: auto;
  padding: 20px 10px;
  display: flex;
}
.cptCoupon input {
}
.cptCoupon button {
  margin-left: 10px;
  background-color: var(--pricipalColor);
  color: white;
}
.cptSum {
}

.cptSum hr {
  border-color: #ccc;
}
.cptSum section {
  width: 100%;
  margin: 20px 0;
  display: flex;
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
}
.cptSum section b {
  font-size: 35px;
}
.cptSum section p {
  color: grey;
}
.cptSum section a {
  background-color: var(--pricipalColor);
  color: white;
  width: 100%;
  text-align: center;
  padding: 10px 0;
}
.cptSum section button {
  padding: 10px 0;
  width: 100%;
}

/* Fin calcul */
