:root {
  --pricipalColor: #717fe0;
}
* {
  box-sizing: border-box;
  z-index: 100;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: "poppins";
}
body {
  /* background-color: white; */
}
h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
p,
span,
button {
  font-family: "poppins";
  border: none;
}
input {
  border: none;
  background-color: transparent;
}
input:focus {
  outline: 0;
}
a {
  text-decoration: none;
}
.semiRounded {
  border-radius: 50px !important;
}
.rounded {
  border-radius: 100%;
}
img {
  object-fit: cover;
}
.addedToCart,
.activeLink {
  color: var(--pricipalColor) !important;
}
.addedToCart {
  animation: addedToCart 0.5s ease 1;
}
.pageBanner {
  display: flex;
  position: relative;
  color: white;
}
.pageBanner h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes addedToCart {
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: initial;
  }
}
#navVisible {
  opacity: 1;
  transform: scale(1);
}
.searchBar {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transform: scale(0);
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.1s ease;
}
.searchBar form {
  width: 50%;
  background-color: white;
  padding: 2px;
  border-radius: 50px;
}
.searchBar input {
  width: 80%;
  padding: 10px 15px;
  border-radius: 50px;
}
.searchBar form button {
  border-radius: 50px;
  width: 20%;
  background-color: rgba(113, 127, 224, 0.8);
}
.searchBar > button:last-child {
  background-color: white;
  margin-left: 10px;
}
/* Menu et navigation */
nav {
  transition: all 0.5s ease;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px 50px;
  box-sizing: border-box;
  position: sticky;
  top: 0;
  z-index: 90000;
}
.nav a {
  color: #4d4d4d;
  transition: color 0.6s ease;
}
.nav a:hover {
  color: var(--pricipalColor);
}

.navLogo a {
  font-size: 30px;
}
.navLogo a span {
  font-size: 35px;
  font-weight: bold;
}
.navLinks a {
  margin-right: 40px;
}
.appNavIconPart {
  margin-left: auto;
}
.navMobileHambuger {
  display: none;
}
/* Sidebar */
.css-1160xiw-MuiPaper-root-MuiDrawer-paper {
  overflow: hidden !important;
  max-width: 30vw;
}
.navSideBar h4,
.navSideBar h6 {
  font-family: "Poppins";
  font-weight: 900;
}
.navSideBar h6 {
  text-decoration: underline;
}
.sbCloser {
  display: flex;
  align-items: center;
  padding: 0 10px;
  justify-content: space-between;
  height: 10vh;
}
.navSideBar ul {
  overflow-y: auto;
  width: 90%;
  margin: auto;
  max-height: 60vh;
}
.navSideBar ul li {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100px;
  width: 100%;
  margin: 0px auto;
  border-radius: 10px;
}
.navSideBar ul li div:nth-child(1) {
  width: 20%;
  height: 100%;
  display: flex;
}
.navSideBar ul li div:nth-child(1) img {
  width: 100%;
  height: 100%;
  background-color: #ccc;
}
.navSideBar ul li > div:nth-child(2) {
  width: 70%;
  margin: auto;
  box-sizing: border-box;
}
.navSideBar ul li div:nth-child(2) span {
  color: var(--pricipalColor);
  font-weight: 600;
}
.navSideBar ul li div:nth-child(2) p {
  color: #909090;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.navSideBar ul li div:nth-child(2) svg {
  color: var(--pricipalColor);
}
.navSideBar ul li div:nth-child(2) div:last-child {
  box-sizing: border-box;
  margin-left: auto !important;
  font-family: "Poppins";
  text-align: right;
}
.nsBottom {
  background-color: var(--pricipalColor);
  color: white;
  margin-top: auto;
  padding: 10px;
  height: 25vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.nsBottom div {
  display: flex;
  justify-content: space-between;
  /* margin: 10px 0; */
}
.nsBottom a {
  text-align: center;
  background-color: white;
  padding: 10px 15px;
  width: 100%;
}
/* Fin sideBar */
/* Fin Menu et Navigation */

/* Footer */
footer {
  overflow: hidden;
  margin-bottom: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 80px 0 40px 0;
}

footer > div {
  width: 22%;
}
footer > div a {
  color: #808080;
}
footer > div h6 {
  padding-left: 15px;
  width: 100%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
  color: white;
}
footer > div form {
  display: flex;
  flex-direction: column;
}
footer > div form button {
  margin: 10px auto 0 0 !important;
}
footer > div form input {
  margin-top: 15px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 15px;
}
footer > div ul li {
  color: #808080;
  /* background-color: red; */
}
footer > div ul svg {
  color: #ccc;
}
footer label {
  color: #ccc !important;
}
.footerFoot {
  width: 90%;
  margin: 20px;
  display: flex;
  justify-content: space-between;
  color: white;
}
.footerFoot img {
  object-fit: contain;
  height: 20px;
}
