/* Bannier accueil */
.accueilBanner {
  height: 90vh;
  background-color: #ccc;
  display: flex;
  align-items: center;
  padding: 0 50px;
  box-sizing: border-box;
}
.abText {
  color: #4d4d4d;
  font-family: "Poppins", sans-serif;
  white-space: nowrap;
}
.abText button {
  padding: 10px 35px;
  background-color: var(--pricipalColor);
  color: white;
}
.accueilBanner img {
  height: 100%;
  margin-left: 10px;
  object-fit: contain;
}

/* Carte */
.productTypeCards {
  margin: 2px auto;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.ptscCard {
  position: relative;
  width: 30%;
  height: 240px;
  background-repeat: no-repeat;
  background-position: top 0px right -50%;
  background-size: 80% auto;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px,
    rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
}
.ptscCard:hover > .ptscCardBtn {
  background-color: rgba(113, 127, 224, 0.8);
  transition: all 0.3s ease-in;
  opacity: 1;
}
.ptscCard:hover > .ptscCardBtn hr {
  transform: scaleX(1);
}
.ptscCard:hover > .ptscCardBtn button {
  transform: initial;
  opacity: 1;
}

.ptscCardBtn {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center !important;
  transition: all 0.2s ease-in;
}
.ptscCardBtn button {
  margin-top: auto;
  margin-right: auto;
  text-align: center;
  width: 100px;
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.5s ease-in;
  color: white;
  font-weight: bold;
  padding: 0 !important;
}
.ptscCardBtn hr {
  border: none;
  height: 3px;
  width: 100px;
  background-color: white;
  margin-bottom: 20%;
  margin-top: 0;
  margin-left: 0;
  transform: scaleX(0);
  transition: transform 0.4s ease-in;
}

@media screen and (max-width: 512px) {
  /* Bannier accueil */
  .accueilBanner {
    position: relative;
    padding: 0;
  }
  .abText,
  .accueilBanner section {
    position: absolute;
    width: 100%;
  }
  .accueilBanner section {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    left: 0;
    right: 0;
  }
  .accueilBanner img {
    width: 100%;
    margin: 0;
  }
  .abText {
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    z-index: 99999;
  }

  /* Carte caategorie de produit */
  .productTypeCards {
    width: 95%;
  }
  .ptscCard {
    width: 100%;
  }
}
