.contactForm {
  width: 80%;
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
  /* background-color: #ccc; */
}

.contactForm p,
.contactForm h6,
.contactForm h4,
.contactForm button {
  font-family: Poppins;
}
.contactForm > div {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  padding: 70px 0;
}
.contactForm div:nth-child(1) {
  text-align: center;
}
.contactForm div:nth-child(1) form {
  width: 85%;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.contactForm div:nth-child(1) form div {
  margin: 10px 0;
}
.contactForm div:nth-child(1) form button {
  padding: 10px 0;
  background-color: royalblue;
  color: white;
}

.contactForm div:nth-child(2) {
}
.contactForm div:nth-child(2) section {
  /* background-color: pink; */
  width: 70%;
  margin: auto;
  display: flex;
  align-items: center;
}
.contactForm div:nth-child(2) section h6 {
  color: #333;
}
.contactForm div:nth-child(2) section p,
.contactForm div:nth-child(2) section a {
  color: #999;
}
.contactForm div:nth-child(2) section svg {
  margin-right: 20px;
}

@media screen and (max-width: 512px) {
  .contactForm {
    width: 95%;
  }
  .contactForm > div {
    width: 100%;
  }
  .contactForm div:nth-child(2) section {
    width: 90%;
  }
}
