.aboutParagraph {
  width: 85%;
  margin: 40px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* background-color: red; */
}

.apText {
  width: 60%;
  display: flex;
  flex-direction: column;
}
.apImg {
  width: 30%;
  position: relative;
  z-index: 10;
  cursor: pointer;
  height: 350px;
}
.apImg::before {
  z-index: -9;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 5%;
  border: solid 2px #ccc;
  border-radius: 5px;
}
#aboutHistory .apImg::before {
  left: -5%;
}
#aboutMissions .apImg::before {
  right: -5%;
}
#aboutHistory {
  margin-bottom: 200px;
}
.apImg div {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}
.apImg:hover img {
  transform: scale(1.2);
}
.apImg img {
  width: 100%;
  height: 100%;
  border-radius: 2px;
  transition: transform 0.8s ease;
}

@media screen and (max-width: 512px) {
  .aboutParagraph {
    flex-direction: column;
  }
  .apText {
    width: 100%;
  }
  .apImg {
    width: 100%;
  }
  #aboutHistory .apText {
    margin-bottom: 30px;
  }
  #aboutMissions .apText {
    margin-top: 30px;
  }
}
