@media screen and (max-width: 512px) {
  /* Navigation */
  nav {
    justify-content: space-between;
    padding: 10px;
  }
  .navLogo {
    order: 2;
  }
  .css-1160xiw-MuiPaper-root-MuiDrawer-paper {
    max-width: 80vw;
  }
  .appNavIconPart {
    order: 1;
    width: 100%;
    text-align: right;
  }
  .navMobileHambuger {
    order: 3;
    display: block;
  }
  nav .navLinks {
    width: 100%;
    display: flex;
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    order: 4;
    background-color: white;
    flex-direction: column;
    margin: auto;
    transition: all 0.5s ease;
  }
  .navLinksVisible {
    opacity: 1 !important;
    max-height: 300px !important;
  }
  nav .navLinks a {
    margin: 0;
    padding: 10px 15px;
  }
  nav .navLinks a:hover {
    background-color: var(--pricipalColor);
    color: white;
  }
  /* Fin menu et navigation */

  /* Bannier accueil */
  .abText {
  }
  /* Fin bannier accueil */

  /* Carte produit */
  .productCard {
    width: 100%;
  }
  /* Fin carte produit */

  /* Modal de produit */
  .productPreview {
    /* flex-direction: column; */
    align-items: inherit;
  }
  .ppImg,
  .ppText {
    width: 90%;
    margin: auto;
  }
  .ppText {
    margin-top: 30%;
  }
  .ppAddToCart button {
    width: 80%;
    margin: auto;
  }
  /* Fin modal de produit */

  /* Page pannier */
  .cpList {
    width: 100%;
  }
  .cpList h2 {
    font-size: 16px;
  }

  .cptHeader {
    flex-direction: column;
  }
  .cptCoupon,
  .cptSum {
    width: 90%;
  }
  /* Fin page pannier */

  /* Page checkout */
  .coForm .formSection {
    width: 100%;
  }
  /* Fin checkout */

  /* Footer */
  footer > div {
    width: 100%;
  }
  footer > div form {
    width: 80%;
    padding-left: 15px;
  }
  footer > div ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  footer > div ul li {
    width: initial !important;
    display: inline-block !important;
  }
  .footerFoot {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .footerFoot img {
    margin: 10px 0;
  }
  /* Fin footer */
}
