/* Modal  */
.modal {
  height: 100vh;
  z-index: 1200000;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  transition: all 0.2s ease;
  transform: scale(0);
}
.productModal {
}
.pmVisible,
.ckmVisible {
  opacity: 1;
  transform: scale(1);
}

/* Modal de produit */
.productPreview {
  width: 90%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  height: 90%;
  overflow: auto;
  background-color: white;
}
.ppClose {
  width: 100%;
  margin: 0;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  border-bottom: solid 1px #ccc;
  position: sticky;
}
.ppImg,
.ppText {
  width: 45%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  height: 90%;
}
.ppImg {
  margin: auto;
}
.ppImg img {
  width: 100%;
  max-height: 80%;
  object-fit: contain;
  margin: auto;
}
.ppText {
  flex-direction: column;
}
.ppText h2 {
  color: var(--pricipalColor);
}
.ppText section {
  display: flex;
  margin: 10px 0;
  align-items: center;
}
.ppCategory {
  justify-content: space-between;
}
.ppUpToCart button,
.ppAddToCart button {
  background-color: var(--pricipalColor);
}
.ppUpToCart > span:nth-child(2) {
  margin: 0 10px;
}
.ppAddToCart button {
  width: 100%;
  padding: 10px;
  border-radius: 50px;
  font-size: 100%;
}
/* Fin produit modal */

/* Modal de paiement */
.checkoutStatus {
  background-color: white;
  margin: auto;
  text-align: center;
  padding: 10px 20px;
}
.checkoutStatus svg {
  color: greenyellow;
  font-size: 45px;
}
.checkoutStatus a,
.checkoutStatus button {
  padding: 10px 15px;
  border-radius: 20px;
  margin: 10px 5px;
}
.checkoutStatus a {
  background-color: var(--pricipalColor);
  color: white;
}
.checkoutStatus button {
  background-color: #ccc;
}
/* Fin modal de paiement */

/* Fin  modal */
